import React from 'react';
import { NavLink } from 'react-router-dom';
import './Inputoptions.css';

function Inputoptions() {
    return (
        <div className="nav-container">
            <div className="nav-options">
                <NavLink to="/inputs">
                    <div className="input-option back"><p><i class="bi bi-arrow-left-short"></i> Volver</p></div>
                </NavLink>
                <NavLink to="/departments">
                    <div className="input-option op-department"><p>Departamentos</p></div>
                </NavLink>
                <NavLink to="/services">
                    <div className="input-option op-service"><p>Servicios</p></div>
                </NavLink>
                <NavLink to="/guard">
                    <div className="input-option op-guard"><p>Guardias</p></div>
                </NavLink>
                <NavLink to="/personal">
                    <div className="input-option op-personal"><p>Personal</p></div>
                </NavLink>
                <NavLink to="/licences">
                    <div className="input-option op-licences"><p>Licencias</p></div>
                </NavLink>
                <NavLink to="/notworking">
                    <div className="input-option op-notworking"><p>Feriados</p></div>
                </NavLink>
            </div>
        </div>
    )
}

export default Inputoptions;