import React, { useEffect, useState } from 'react';
import instance from '../../axios'


function DepartmentForm(props) {
    const csrftoken = window.CSRF_TOKEN;
    const [name, setName] = useState('')
    const id = props.id


    function updateIf(){
            if (props.name){
                setName(props.name)
        }
    }

    useEffect(() =>{
        updateIf()
    },[props.name])
    
   /* ------------ SAVE / UPDATE DEPARTMENT -------------*/

    function saveDepartment(e) {
        e.preventDefault();
        if (props.id){
            const postData = {
                "id": id,
                "name": name
            };
            instance.put('/department/', postData).then((response) => { props.after(response.data) })
            setName('')
        } else {
            const postData = {
            name
            };
            instance.post('/department/', postData).then((response) => { props.after(response.data) })
            setName('')
        };
    }

    /* ------------ DEPARTMENT COMPONENT -------------*/

    return (
       <div className="department-form">
            {id ? <p>Modificar departamento</p> : <p>Crear nuevo departamento</p>}
            <form method="post" className="form-input" onSubmit={saveDepartment}>
                <input name="csrfmiddlewaretoken" value={csrftoken} hidden />
                
                <input className="Inputext" type="text" name="dep_name" id="dep_name" value={name} placeHolder="Nombre de departamento" onChange={(e) => setName(e.target.value)} required />
                <button type="submit" className="btn btn-outline-success btn-sm" onClick={() => (window.confirm(`¿Confirma guardar "${name}"?`))} disabled={name.length < 1}>Guardar</button>
            </form>
        </div>
    )
}

export default DepartmentForm;