import React, { useState, useEffect } from 'react';
import GuardForm from './GuardForm';
import Goback from '../../UI/Goback';
import UseToggle from '../../../UseToggle'
import './Guards.css';
import Inputoptions from '../Inputoptions';
import instance from '../../axios';
import { useTransition, animated } from 'react-spring';





function Guard() {

    const [show, setShow] = UseToggle();
    const transition = useTransition(show, {
        from:{opacity: 0, height:0},
        enter:{height:window.innerWidth < 991 ? 210 : 190},
        update:{opacity:1},
        leave: item => async (next, cancel) =>{
            await next ({opacity: 0})
            await next ({height:0, y: -50})
        },
        config: {duration: 150}
    })
    const [servList, setServlist] = useState([]);
    const [depList, setDeplist] = useState([]);
    const [guardName, setGuardName] = useState();
    const [guardId, setGuardId] = useState();
    const [guardData, setGuardData] = useState();
    const [selectedServ, setSelectedServ] = useState("");
    const [guardList, setGuardList] = useState([]);


    function after(response) {
        if (show) {
            setShow()
        }
        updateList(selectedServ);
        alert(response);
    }

    /* --------------- GUARD LIST --------------*/

    function updateDep() {
        instance.get('/department/')
            .then(res => {
                setDeplist(res.data)
            })
            .catch(Error)
    }

    function updateServ(id) {
        instance.get('/service/', { params: { department_id: id } })
            .then(res => {
                setServlist(res.data)
                document.getElementById("selServ").selectedIndex = 0
            })
            .catch(Error)
    }

    function updateList(id) {
        setSelectedServ(id)
        instance.get('/guard/', { params: { service_id: id } })
            .then(res => {
                setGuardList(res.data)
            })
            .catch(Error)
    }

    useEffect(() => {
        updateDep()
    }, [])

    /* ------------ GUARD ELEMENT ID -------------*/

    function addValue(id, name) {
        const selGuardData = guardList.filter((obj) => obj.id === Number(id))
        setGuardData(selGuardData);
        setGuardName(name);
        setGuardId(id);
        if (!show) {
            setShow()
        }

    }

    /*-------------- GUARD ELEMENT-------------------*/


    function deleteElement(e) {
        instance.delete('/guard/' + e)
            .then((response) => { after(response.data) });
    }

    /*-------------------GUARD VALUES------------------*/

    function resetValues() {
        setShow();
        setGuardName();
        setGuardId();
    }

    return (
        <div className="gua">
            <Goback />
            <Inputoptions />
            <div className="guard">
                <div className="input-name">
                    <div className="input-bar">
                        <span>Guardias</span>
                        
                            <select  className="form-select form-select-sm" onChange={event => updateServ(event.target.value)} required>
                                <option value="" disabled hidden selected>Selecione departamento</option>
                                {depList.map(dep => (
                                    <option key={dep.id} value={dep.id}>{dep.name}</option>
                                ))}
                            </select>
                            <select id="selServ"  className="form-select form-select-sm" onChange={event => updateList(event.target.value)} required>
                                <option value="" disabled hidden selected>Selecione servicio</option>
                                {servList.map(dep => (
                                    <option key={dep.id} value={dep.id}>{dep.name}</option>
                                ))}
                            </select>
                        
                        <button className="btn btn-outline-success new-entry" onClick={selectedServ.length > 0 ? resetValues : null}>{selectedServ.length > 0 && show ? <div className="button">Cancelar</div> : <div className="button">Nuevo</div>}</button>
                    </div>
                    
                    {transition((style, item) =>
                    item ? <animated.div style={style}><GuardForm name={guardName} id={guardId} servId={selectedServ} data={guardData} after={after}/></animated.div> : null)}
                </div>

                <table className="table">
                    <thead>
                        <tr>
                            <th className="th-name" scope="col">Guardia</th>
                            <th className="th-option" scope="col">Opciones</th>
                        </tr>
                    </thead>
                    <tbody>
                        {guardList.map(guard => (
                            <tr key={guard.id}>
                                <td className="td-name" name={guard.id} value={guard.name}>{guard.name}</td>
                                <td className="td-option">
                                    <button key={guard.id} className="btn btn-outline-success"
                                        id={guard.id}
                                        name={guard.name}
                                        onClick={(e) => addValue(e.currentTarget.id, e.currentTarget.name)}>
                                        <i class="bi bi-pencil-square"></i></button>
                                    <button key={guard.id} className="btn btn-outline-success"
                                        value={guard.id}
                                        id={guard.id}
                                        onClick={(e) => { if (window.confirm('¿Confirma eliminar?')) { deleteElement(e.currentTarget.id) } }}>
                                        <i class="bi bi-trash"></i></button></td>
                            </tr>
                        ))}

                    </tbody>
                </table>
                 {guardList.length === 0 && !selectedServ ? <p className="not-list"><i class="bi bi-info-circle"></i> Seleccione departamento y servicio correspondiente</p> : null }
                {guardList.length === 0 && selectedServ ? <p className="not-list"><i class="bi bi-info-circle"></i> No existen registros</p> : null }
            </div>
        </div>
    )
}

export default Guard;