import React, { useState, useEffect } from 'react';
import PersonalForm from './PersonalForm';
import Goback from '../../UI/Goback';
import UseToggle from '../../../UseToggle'
import './Personal.css';
import Inputoptions from '../Inputoptions';
import instance from '../../axios';
import { useTransition, animated } from 'react-spring';




function Personal() {

    const [show, setShow] = UseToggle();
    const transition = useTransition(show, {
        from:{opacity: 0, height:0},
        enter:{height:window.innerWidth < 991 ? 380 : 235},
        update:{opacity:1},
        leave: item => async (next, cancel) =>{
            await next ({opacity: 0})
            await next ({height:0, y: -50})
        },
        config: {duration: 150}
    })
    const [servList, setServlist] = useState([]);
    const [depList, setDeplist] = useState([]);
    const [personalName, setPersonalName] = useState();
    const [personalId, setPersonalId] = useState();
    const [selectedServ, setSelectedServ] = useState("");
    const [personalList, setPersonalList] = useState([]);


    function after(response) {
        if (show) {
            setShow()
        }
        updateList(selectedServ);
        alert(response);
    }

    /* --------------- PERSONAL LIST --------------*/

    function updateDep() {
        instance.get('/department/')
            .then(res => {
                setDeplist(res.data)
            })
            .catch(Error)
    }

    function updateServ(id) {
        instance.get('/service/', { params: { department_id: id } })
            .then(res => {
                setServlist(res.data)
                document.getElementById("selServ").selectedIndex = 0
            })
            .catch(Error)
    }

    function updateList(id) {
        setSelectedServ(id)
        instance.get('/personal/', { params: { service_id: id } })
            .then(res => {
                setPersonalList(res.data)
            })
            .catch(Error)
    }

    useEffect(() => {
        updateDep()
    }, [])

    /* ------------ PERSONAL ELEMENT ID -------------*/

    function addValue(id) {
        const personData = personalList.filter((obj) => obj.id === Number(id))
        setPersonalName(personData);
        setPersonalId(id);
        if (!show) {
            setShow()
        }

    }

    /*-------------- PERSONAL ELEMENT-------------------*/


    function deleteElement(e) {
        instance.delete('/personal/' + e)
            .then((response) => { after(response.data) });
    }

    /*-------------------PERSONAL VALUES------------------*/

    function resetValues() {
        setShow();
        setPersonalName();
        setPersonalId();
    }

    return (
        <div className="per">
            <Goback />
            <Inputoptions />
            <div className="personal">
                <div className="input-name">
                    <div className="input-bar">
                        <span>Personal</span>
                        
                            <select className="form-select form-select-sm"  onChange={event => updateServ(event.target.value)} required>
                                <option value="" disabled hidden selected>Selecione departamento</option>
                                {depList.map(dep => (
                                    <option key={dep.id} value={dep.id}>{dep.name}</option>
                                ))}
                            </select>
                            <select className="form-select form-select-sm"  id="selServ" onChange={event => updateList(event.target.value)} required>
                                <option value="" disabled hidden selected>Selecione servicio</option>
                                {servList.map(dep => (
                                    <option key={dep.id} value={dep.id}>{dep.name}</option>
                                ))}
                            </select>
                        
                        <button className="btn btn-outline-success new-entry" onClick={selectedServ.length > 0 ? resetValues : null}>{selectedServ.length > 0 && show ? <div className="button">Cancelar</div> : <div className="button">Nuevo</div>}</button>
                    </div>
                    {transition((style, item) =>
                    item ? <animated.div style={style}><PersonalForm 
                        name={personalName} 
                        id={personalId} 
                        servId={selectedServ} 
                        after={after}/></animated.div> : null)}
                    
                </div>

                <table className="table">
                    <thead>
                        <tr>
                            <th className="th-name" scope="col">Nombre y Apellido</th>
                            <th className="th-option" scope="col">Opciones</th>
                        </tr>
                    </thead>
                    <tbody>
                        {personalList.map(person => (
                            <tr key={person.id}>
                                <td className="td-name" name={person.id} value={person.name}>{person.file} - {person.last_name}, {person.name} - {person.profession}</td>
                                <td className="td-option">
                                    <button key={person.id} className="btn btn-outline-success"
                                        id={person.id}
                                        name={person.name}
                                        onClick={(e) => addValue(e.currentTarget.id, e.currentTarget.name)}>
                                        <i class="bi bi-pencil-square"></i></button>
                                    <button key={person.id} className="btn btn-outline-success"
                                        value={person.id}
                                        id={person.id}
                                        onClick={(e) => { if (window.confirm('¿Confirma eliminar?')) { deleteElement(e.currentTarget.id) } }}>
                                        <i class="bi bi-trash"></i></button></td>
                            </tr>
                        ))}

                    </tbody>
                </table>
                {selectedServ ? null : <p className="not-list"><i class="bi bi-info-circle"></i> Seleccione departamento y servicio correspondiente</p>}
                {personalList.length === 0 && selectedServ ? <p className="not-list"><i class="bi bi-info-circle"></i> No existen registros</p> : null }
            </div>
        </div>
    )
}

export default Personal;