import React, { useState, useEffect } from 'react';
import ServiceForm from './ServiceForm';
import Goback from '../../UI/Goback';
import useToggle from '../../../UseToggle';
import './Services.css';
import instance from '../../axios';
import Inputoptions from '../Inputoptions';
import { useTransition, animated } from 'react-spring';


function Services() {

    const [show, setShow] = useToggle();
    const transition = useTransition(show, {
        from:{opacity: 0, height:0},
        enter:{height:window.innerWidth < 991 ? 165 : 110},
        update:{opacity:1},
        leave: item => async (next, cancel) =>{
            await next ({opacity: 0})
            await next ({height:0, y: -50})
        },
        config: {duration: 150}
    })
    const [servList, setServlist] = useState([]);
    const [depList, setDeplist] = useState([]);
    const [servName, setServName] = useState();
    const [servId, setServId] = useState();
    const [selected, setSelected] = useState("");
    
    function after(response) {
        if (show){
            setShow()
        }
        updateList(selected);
        alert(response);
    }

    /* --------------- UPDATE LIST --------------*/

    function updateDep() {
        instance.get('/department/')
            .then(res => {
                setDeplist(res.data)
            })
            .catch(Error)
    }

    function updateList(id) {

        instance.get('/service/', { params: {department_id: id} })
            .then(function (res) {
                setServlist(res.data)
                setServId(id);
            })
            .catch(Error)
    }

    useEffect(() => {
        updateDep()
    }, [])

    /* ------------ UPDATE ELEMENT ID -------------*/

    function addValue(id, name) {
        setServName(name);
        setServId(id);
        if (!show) {
            setShow()
        }

    }

    /*-------------- DELETE ELEMENT-------------------*/


    function deleteElement(e) {
        instance.delete('/service/' + e)
            .then((response) => { after(response.data) });
            
    }

    /*-------------------RESET VALUES------------------*/

    function resetValues() {
        setShow();
        setServName();
        setServId();
    }

    /*-------------------UPDATE SERVICE LIST------------------*/

    function updateServiceList(id){
        setSelected(id)
        updateList(Number(id))
        
    }

    
    


    return (
        <div className="ser">
            <Goback />
            <Inputoptions />
            <div className="services">

                <div className="input-name">
                <div className="input-bar">
                <span>Servicios</span>
                    <select className="form-select form-select-sm" onChange={(e) => updateServiceList(e.target.value)} required>
                        <option value="" disabled hidden selected>Selecione departamento</option>
                        {depList.map(dep => (
                            <option key={dep.id} value={dep.id}>{dep.name}</option>
                        ))}
                    </select>
                    <button className="btn btn-outline-success new-entry" onClick={selected.length > 0 ? resetValues : null}>{selected.length > 0 && show ? <div className="button">Cancelar</div> : <div className="button">Nuevo</div>}</button>
                </div>
                    
                    {transition((style, item) =>
                    item ? <animated.div style={style}><ServiceForm name={servName} id={servId} depId={selected} after={after} /></animated.div> : null)}
                </div>

                 <table className="table">
                    <thead>
                        <tr>
                            <th className="th-name" scope="col">Servicios</th>
                            <th className="th-option" scope="col">Opciones</th>
                        </tr>
                    </thead>
                    <tbody>
                        {servList.map(serv => (
                            <tr key={serv.id}>
                                <td className="td-name" name={serv.id} value={serv.name}>{serv.name}</td>
                                <td className="td-option">
                                    <button key={serv.id} className="btn btn-outline-success"
                                        id={serv.id}
                                        name={serv.name}
                                        onClick={(e) => addValue(e.currentTarget.id, e.currentTarget.name)}>
                                        <i class="bi bi-pencil-square"></i></button>
                                    <button key={serv.id} className="btn btn-outline-success"
                                        value={serv.id}
                                        id={serv.id}
                                        onClick={(e) => { if (window.confirm('¿Confirma eliminar?')) { deleteElement(e.currentTarget.id) } }}>
                                        <i class="bi bi-trash"></i></button></td>
                            </tr>
                        ))}
                        
                    </tbody>
                </table>
                {servList.length === 0 && !servId ? <p className="not-list"><i class="bi bi-info-circle"></i> Seleccione departamento</p> : null }
                {servList.length === 0 && servId ? <p className="not-list"><i class="bi bi-info-circle"></i> No existen registros</p> : null }

            </div>
        </div>
    )
}



export default Services;