import React, { useState, useEffect } from 'react';
import instance from '../../axios';


function PersonalForm(props) {
    const csrftoken = window.CSRF_TOKEN;
    const [name, setName] = useState("");
    const [lastName, setLastName] = useState("");
    const [file, setFile] = useState("");
    const [d, setD] = useState("");
    const [profession, setProfession] = useState("");
    const [phone, setPhone] = useState("");
    const id = props.id
    const servId = Number(props.servId)


    function updateIf(){
            if (props.name){
                console.log(props.name)
                setName(props.name[0].name)
                setLastName(props.name[0].last_name)
                setD(props.name[0].d)
                setFile(props.name[0].file)
                setProfession(props.name[0].profession)
                setPhone(props.name[0].phone)
        }
    }

    useEffect(() =>{
        updateIf()
    },[props.name])
    
   /* ------------ SAVE / UPDATE DEPARTMENT -------------*/

    function saveGuard(e) {
        e.preventDefault();
        if (props.id){
            const postData = {
                "id": id,
                "name": name,
                "last_name": lastName,
                "file": file,
                "d": d,
                "profession": profession,
                "phone": phone,
                "service_id": servId
            };
            instance.put('/personal/', postData).then((response) => { props.after(response.data) })
            setName('')
            setLastName('')
            setD('')
            setFile('')
            setProfession('')
            setPhone('')
        } else {
            const postData = {
                "name": name,
                "last_name": lastName,
                "file": file,
                "d": d,
                "profession": profession,
                "phone": phone,
                "service_id": servId
            };
            console.log(postData)
            instance.post('/personal/', postData).then((response) => { props.after(response.data) })
            setName('')
            setLastName('')
            setD('')
            setFile('')
            setProfession('')
            setPhone('')
        };
    }

    return (
        <div className="personal-form">
        {id ? <p>Modificar personal</p> : <p>Crear nuevo personal</p>}
            <form method="post" className="form-input" onSubmit={saveGuard}>
                <input name="csrfmiddlewaretoken" value={csrftoken} hidden />
                
                <input className="Inputext" type="text" name="username" value={name} placeHolder="Nombre" onChange={(e) => setName(e.target.value)} required />
                <input className="Inputext" type="text" name="username" value={lastName} placeHolder="Apellido" onChange={(e) => setLastName(e.target.value)} required />
                <input className="Inputext" type="text" name="username" value={d} placeHolder="D" onChange={(e) => setD(e.target.value)} required />
                <input className="Inputext" type="text" name="username" value={file} placeHolder="Legajo" onChange={(e) => setFile(e.target.value)} required />
                <input className="Inputext" type="text" name="username" value={profession} placeHolder="Profesión" onChange={(e) => setProfession(e.target.value)} required />
                <input className="Inputext" type="text" name="username" value={phone} placeHolder="Telefono" onChange={(e) => setPhone(e.target.value)} required />
                <div className="selected">
                
                </div>
                
                <button type="submit" className="btn btn-outline-success btn-sm"
                disabled={
                    name.length < 1 || 
                    lastName.length < 1 || 
                    file.length < 1 ||
                    d.length < 1 ||
                    profession.length < 1 ||
                    phone.length < 1
                    }>Guardar</button>
                
                
            </form>
        </div>
    )
}

export default PersonalForm;