import React, { useState, useEffect } from 'react';
import LicencesForm from './LicencesForm';
import Goback from '../../UI/Goback';
import UseToggle from '../../../UseToggle';
import './Licences.css';
import instance from '../../axios';
import Inputoptions from '../Inputoptions';
import { useTransition, animated } from 'react-spring';

function Licences() {

    const [show, setShow] = UseToggle();
    const transition = useTransition(show, {
        from:{opacity: 0, height:0},
        enter:{height:window.innerWidth < 991 ? 170 : 110},
        update:{opacity:1},
        leave: item => async (next, cancel) =>{
            await next ({opacity: 0})
            await next ({height:0})
        },
        config: {duration: 150}
    })
    const [licencesList, setLicencesList] = useState([]);
    const [licencesName, setLicencesName] = useState();
    const [licencesId, setLicencesId] = useState();

    function after(response) {
        if (show){
            setShow()
        }
        updateList();
        alert(response);
    }

    /* --------------- UPDATE LIST --------------*/

    function updateList() {
        instance.get('/licences/')
            .then(res => {
                setLicencesList(res.data)
            })
            .catch(Error)
    }

    useEffect(() => {
        updateList()
    }, [])

    /* ------------ UPDATE ELEMENT ID -------------*/

    function addValue(id, name) {
        setLicencesName(name);
        setLicencesId(id);
        if (!show) {
            setShow()
        }

    }

    /*-------------- DELETE ELEMENT-------------------*/


    function deleteElement(e) {
        instance.delete('/licences/' + e)
            .then((response) => { after(response.data) });
    }

    /*-------------------RESET VALUES------------------*/

    function resetValues() {
        setShow();
        setLicencesName();
        setLicencesId();
    }

    /* ------------ DEPARTMENT COMPONENT -------------*/

    return (
        <div className="lic">
            <Goback />
            <Inputoptions />
            <div className="licences">
                <div className="input-name">
                    <div className="input-bar">
                        <span>Licencias</span>
                        <button className="btn btn-outline-success new-entry" onClick={resetValues}>{show ? <div className="button">Cancelar</div> : <div className="button">Nuevo</div>}</button>
                    </div>
                   
                    {transition((style, item) =>
                    item ? <animated.div style={style}><LicencesForm name={licencesName} id={licencesId} after={after} /></animated.div> : null)}
                </div>


                <table className="table">
                    <thead>
                        <tr>
                            <th className="th-name" scope="col">Licencias</th>
                            <th className="th-option" scope="col">Opciones</th>
                        </tr>
                    </thead>
                    <tbody>
                        {licencesList.map(dep => (
                            <tr key={dep.id}>
                                <td className="td-name" name={dep.id} value={dep.name}>{dep.name}</td>
                                <td className="td-option">
                                    <button key={dep.id} className="btn btn-outline-success"
                                        id={dep.id}
                                        name={dep.name}
                                        onClick={(e) => addValue(e.currentTarget.id, e.currentTarget.name)}>
                                        <i class="bi bi-pencil-square"></i></button>
                                    <button key={dep.id} className="btn btn-outline-success"
                                        value={dep.id}
                                        id={dep.id}
                                        onClick={(e) => { if (window.confirm('¿Confirma eliminar?')) { deleteElement(e.currentTarget.id) } }}>
                                        <i class="bi bi-trash"></i></button></td>
                            </tr>
                        ))}

                    </tbody>
                </table>
                {licencesList.length > 0 ? null : <p className="not-list"><i class="bi bi-info-circle"></i> No hay registros</p>}
            </div>
        </div>
    )
}

export default Licences;