import React, { useState } from 'react';
import { Navbar, Nav } from 'react-bootstrap';
import { NavLink } from 'react-router-dom';
import './Navbar.css'
import logo from '../../assets/images/logo.svg';


function Navbar2() {
    const [expanded, setExpanded] = useState(false)
    return (
        <Navbar bg="light" expand="lg" expanded={expanded}>
            <div className="container-nav">
            <Navbar.Brand><img src={logo} className="logo" alt='logo' /></Navbar.Brand>
            <Navbar.Toggle onClick={() => setExpanded(expanded ? false : "expanded")} />
            <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="mr-auto">
                    <NavLink to="/inputs" onClick={() => setExpanded(false)}><i className="bi bi-house" disabled></i>Inicio</NavLink>
                    <NavLink to="/inputs" onClick={() => setExpanded(false)}><i className="bi bi-calendar3" disabled></i>Planificación</NavLink>
                    <NavLink to="/inputs" onClick={() => setExpanded(false)}><i className="bi bi-file-earmark-bar-graph" disabled></i>Informes</NavLink>
                    <NavLink to="/inputs" onClick={() => setExpanded(false)}><i className="bi bi-people" disabled></i>Personal</NavLink>
                    <NavLink to="/inputs" onClick={() => setExpanded(false)}><i className="bi bi-archive" ></i>Carga de datos</NavLink>
                </Nav>
            </Navbar.Collapse>
            </div>
        </Navbar>

    )
}

export default Navbar2