import React, { useState, useEffect } from 'react';
import NotWorkingForm from './NotWorkingForm';
import Goback from '../../UI/Goback';
import UseToggle from '../../../UseToggle';
import './NotWorking.css';
import instance from '../../axios';
import Inputoptions from '../Inputoptions';
import { useTransition, animated } from 'react-spring';

function Departments() {

    const [show, setShow] = UseToggle();
    const transition = useTransition(show, {
        from:{opacity: 0, height:0},
        enter:{height:window.innerWidth < 991 ? 220 : 180},
        update:{opacity:1},
        leave: item => async (next, cancel) =>{
            await next ({opacity: 0})
            await next ({height:0})
        },
        config: {duration: 150}
    })
    const [notworkingList, setNotworkinglist] = useState([]);
    const [notworkingName, setNotworkingName] = useState();
    const [notworkingData, setNotworkingData] = useState();
    const [notworkingId, setNotworkingId] = useState();

    function after(response) {
        if (show){
            setShow()
        }
        updateList();
        alert(response);
    }

    /* --------------- UPDATE LIST --------------*/

    function updateList() {
        instance.get('/notworking/')
            .then(res => {
                setNotworkinglist(res.data)
            })
            .catch(Error)
    }

    useEffect(() => {
        updateList()
    }, [])

    /* ------------ UPDATE ELEMENT ID -------------*/

    function addValue(id, name) {
        const selNotworkingData = notworkingList.filter((obj) => obj.id === Number(id))
        setNotworkingData(selNotworkingData);
        setNotworkingName(name);
        setNotworkingId(id);
        if (!show) {
            setShow()
        }

    }

    /*-------------- DELETE ELEMENT-------------------*/


    function deleteElement(e) {
        instance.delete('/department/' + e)
            .then((response) => { after(response.data) });
    }

    /*-------------------RESET VALUES------------------*/

    function resetValues() {
        setShow();
        setNotworkingName();
        setNotworkingId();
    }

    /* ------------ DEPARTMENT COMPONENT -------------*/

    return (
        <div className="nw">
            <Goback />
            <Inputoptions />
            <div className="notworking">
                <div className="input-name">
                    <div className="input-bar">
                        <span>Días no laborables</span>
                        <button className="btn btn-outline-success new-entry" onClick={resetValues}>{show ? <div className="button">Cancelar</div> : <div className="button">Nuevo</div>}</button>
                    </div>
                    
                    {transition((style, item) =>
                    item ? <animated.div style={style}><NotWorkingForm name={notworkingName} data={notworkingData} id={notworkingId} after={after} /></animated.div> : null)}
                </div>


                <table className="table">
                    <thead>
                        <tr>
                            <th className="th-name" scope="col">Días no laborables</th>
                            <th className="th-option" scope="col">Opciones</th>
                        </tr>
                    </thead>
                    <tbody>
                        {notworkingList.map(nw => (
                            <tr key={nw.id}>
                                <td className="td-name" name={nw.id} value={nw.name}>{nw.date} - {nw.name}</td>
                                <td className="td-option">
                                    <button key={nw.id} className="btn btn-outline-success"
                                        id={nw.id}
                                        name={nw.name}
                                        onClick={(e) => addValue(e.currentTarget.id, e.currentTarget.name)}>
                                        <i class="bi bi-pencil-square"></i></button>
                                    <button key={nw.id} className="btn btn-outline-success"
                                        value={nw.id}
                                        id={nw.id}
                                        onClick={(e) => { if (window.confirm('¿Confirma eliminar?')) { deleteElement(e.currentTarget.id) } }}>
                                        <i class="bi bi-trash"></i></button></td>
                            </tr>
                        ))}

                    </tbody>
                </table>
                {notworkingList.length > 0 ? null : <p className="not-list"><i class="bi bi-info-circle"></i> No hay registros</p>}
            </div>
        </div>
    )
}

export default Departments;