import React from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import { TransitionGroup, CSSTransition } from 'react-transition-group';
import Datainput from './components/Datainput';
import Error from './components/UI/Error';
import Navbar from './components/UI/Navbar';
import Departments from './components/datainput/department/Departments';
import Services from './components/datainput/service/Services';
import Guard from './components/datainput/guard/Guard';
import Personal from './components/datainput/personal/Personal';
import NotWorking from './components/datainput/notworking/NotWorking';
import Licences from './components/datainput/licences/Licences';
import Home from './components/Home';

function Router() {

   
    return (
        <BrowserRouter>
            <Navbar />
            <Route render={({location}) => (
                <TransitionGroup>
                <CSSTransition
                key={location.key}
                timeout={300}
                classNames='fade'
                >
                    <Switch location={location}>
                        <Route exact path="/" component={Home} />
                        <Route exact path="/inputs" component={Datainput} />
                        <Route exact path="/departments" component={Departments} />
                        <Route exact path="/licences" component={Licences} />
                        <Route exact path="/notworking" component={NotWorking} />
                        <Route exact path="/services" component={Services} />
                        <Route exact path="/guard" component={Guard} />
                        <Route exact path="/personal" component={Personal} />
                        <Route exact component={Error} />
                    </Switch>
                </CSSTransition>
            </TransitionGroup>
            )} />
            
        </BrowserRouter>
    )
}

export default Router;