import React from 'react';
import './Datainput.css';
import { NavLink } from 'react-router-dom';

function Datainput() {

    return (
        <div className="datainput">

            <h3>Carga de datos</h3>
            <div className="row options">

                <div className="col-6">
                    <NavLink to="/departments">
                        <div className="input-button">
                            <div className="button-title">
                                <i class="bi bi-building icon"></i><span>Departamentos</span>
                            </div>
                        </div>
                    </NavLink>
                </div>

                <div className="col-6">
                    <NavLink to="/services">
                        <div className="input-button">
                            <div className="button-title">
                                <i class="bi bi-door-open"></i><span>Servicios</span>
                            </div>
                        </div>
                    </NavLink>
                </div>

            </div>
            <div className="row options">
                <div className="col-6">
                    <NavLink to="/guard">
                        <div className="input-button">
                            <div className="button-title">
                                <i class="bi bi-shield-plus"></i><span>Guardias, Turnos...</span>
                            </div>
                        </div>
                    </NavLink>
                </div>
                <div className="col-6">
                    <NavLink to="/personal">
                        <div className="input-button">
                            <div className="button-title">
                                <i class="bi bi-person"></i><span>Personal</span>
                            </div>
                        </div>
                    </NavLink>
                </div>
            </div>
            <div className="row options">
                <div className="col-6">
                    <NavLink to="/licences">
                        <div className="input-button">
                            <div className="button-title">
                                <i class="bi bi-file-earmark-medical"></i><span> Licencias</span>
                            </div>
                        </div>
                    </NavLink>
                </div>
                <div className="col-6">
                    <NavLink to="/notworking">
                        <div className="input-button">
                            <div className="button-title">
                                <i class="bi bi-calendar2-minus"></i><span>Feriados</span>
                            </div>
                        </div>
                    </NavLink>
                </div>
            </div>

        </div>
    )
}

export default Datainput;