import React, { useEffect, useState } from 'react';
import instance from '../../axios'

function NotWorkingForm(props) {
    const csrftoken = window.CSRF_TOKEN;
    const [name, setName] = useState('')
    const [date, setDate] = useState('')
    const id = props.id


    function updateIf(){
            if (props.name){
                setName(props.name)
                document.getElementById("date").value = props.data[0].date
        }
    }

    useEffect(() =>{
        updateIf()
    },[props.name])
    
   /* ------------ SAVE / UPDATE DEPARTMENT -------------*/

    function saveNotworking(e) {
        e.preventDefault();
        if (props.id){
            const postData = {
                "id": id,
                "name": name,
                "date": date
            };
            instance.put('/notworking/', postData).then((response) => { props.after(response.data) })
            setName('')
        } else {
            const postData = {
            name,
            date
            };
            instance.post('/notworking/', postData).then((response) => { props.after(response.data) })
            setName('')
        };
    }

    /* ------------ DEPARTMENT COMPONENT -------------*/

    return (
        <div className="notworking-form">
            {id ? <p>Modificar feriado</p> : <p>Crear nuevo feriado</p>}
            <form method="post" className="form-input" onSubmit={saveNotworking}>
                <input name="csrfmiddlewaretoken" value={csrftoken} hidden />
                <input className="Inputext" type="text" value={name} placeHolder="Razón del Feriado" onChange={(e) => setName(e.target.value)} required />
                <input className="Inputext" type="date" id="date" placeHolder="Fecha" onChange={(e) => setDate(e.target.value)} required />
                <br />
                <button type="submit" className="btn btn-outline-success btn-sm" onClick={() => (window.confirm(`¿Confirma guardar "${name}"?`))} disabled={name.length < 1 || date.length < 1 }>Guardar</button>
            </form>
        </div>
    )
}

export default NotWorkingForm;